$section-ext-dt-bottom-margin:          var(--#{$prefix}margin-sm) !default;
$section-ext-dd-bottom-margin:          var(--#{$prefix}margin) !default;
$section-ext-dt-font-weight:            $font-weight-bold !default;
$section-ext-dt-font-size:              var(--#{$prefix}body-font-size) !default;
$section-ext-dt-font-family:            var(--#{$prefix}body-font-family) !default;
$section-ext-dd-left-padding:           calc(3 * var(--#{$prefix}spacer)) !default;
$section-ext-dl-table-border:           1px solid var(--#{$prefix}grey) !default;
$section-ext-dl-table-padding:          var(--#{$prefix}margin) var(--#{$prefix}margin-sm) !default;
$section-ext-dl-table-highlight-color:  inherit !default;

.type-section-ext {

    &.as-li,
    &.as-div {
        .subsection {
            margin-bottom: var(--#{$prefix}element-margin);

            // special margin adjustments for section pieces
            &.piece {
                // if a text-only piece element fowllows a subsection, reduce top margin
                // goal: make text from 2nd element apperar as if it belongs to first element
                &.only-text:not(:first-child) {
                    margin-top: calc(var(--#{$prefix}margin) - var(--#{$prefix}element-margin));
                }
                &.only-heading {
                    // if piece element has only a header, set margin the same height as normally used by the headers
                    margin-bottom: var(--#{$prefix}headings-margin-bottom);
                    + .only-text {
                        margin-top: 0;
                    }
                }
                &:last-child {
                    margin-bottom: 0;
                }
                &.flex {
                    .body {
                        // fixes an edge case with automatic line breaks
                        overflow: hidden;
                    }
                }
            }
        }

        &.dl-headings {
            .subsections > .subsection .piece-heading {
                font-family: $section-ext-dt-font-family;
                font-weight: $section-ext-dt-font-weight;
                font-size: $section-ext-dt-font-size;
                line-height: $line-height-base;
                margin-bottom: $section-ext-dt-bottom-margin;
            }
        }
    }

    &.as-li {
        > ul {
            list-style-type: none;
            margin: 0;
            padding: 0;
        }
    }

    &.as-dl,
    &.as-dl-table,
    &.as-dl-cols  {

        > dl {
            margin-bottom: 0;
            > div {
                > dt {
                    font-family: $section-ext-dt-font-family;
                    font-weight: $section-ext-dt-font-weight;
                    font-size: $section-ext-dt-font-size;
                    line-height: $line-height-base;
                    @include use-hyphens-headings;
                }
                > dd {
                    margin-bottom: 0;
                }
            }

            .btn,
            .piece-text-link {
                white-space: normal;
                @include use-hyphens-text;
            }

            .btn {
                text-align: left;
                &.btn-block {
                    text-align: center;
                }
            }
        }

        // assuming the icon is rather small here
        // adjust the zoomer icon position
        .effect-box .zoomer::after,
        .effect-piece .zoomer::after {
            right: 4px;
            bottom: 2px;
            font-size: 14px;
        }
    }

    &.as-dl,
    &.as-dl-cols {
        > dl > div {

            > dt {
                margin-bottom: $section-ext-dt-bottom-margin;
            }
            > dd {
                margin-bottom: $section-ext-dd-bottom-margin;
                padding-left: $section-ext-dd-left-padding;
            }
            &.single {
                > dt {
                    margin-bottom: 0;
                }
            }
            &:last-of-type {
                > dd {
                    margin-bottom: 0;
                }
            }
        }
    }

    &.as-dl-table {
        > dl > div {
            &:nth-child(even) {
                background-color: $section-ext-dl-table-highlight-color;
            }

            > dt, dd {
                flex-grow: 1;
                border-bottom: $section-ext-dl-table-border;
                border-right: $section-ext-dl-table-border;
                padding: $section-ext-dl-table-padding;
            }
        }
    }

    @include media-breakpoint-down($piece-desktop-breakpoint) {

        &.as-dl-table {
            > dl > div {
                border-top: $section-ext-dl-table-border;
                border-left: $section-ext-dl-table-border;

                + div {
                    margin-top: var(--#{$prefix}margin);
                }
            }
        }
    }

    @include media-breakpoint-up($piece-desktop-breakpoint) {

        &.as-dl-cols,
        &.as-dl-table {
            > dl {
                display: flex;
                flex-direction: column;

                > div {
                    display: flex;

                    > dd > .piece {
                        flex-grow: 1;
                    }

                    > dt, dd {
                        display: flex;
                        vertical-align: top;
                    }
                    > dt {
                        max-width: 25%;
                        min-width: 25%;
                    }

                    &.single {
                        > dt {
                            max-width: none;
                            min-width: none;
                        }
                    }
                }
            }
        }

        &.as-dl-cols {
            > dl > div {
                > dt {
                    padding-right: $section-ext-dl-table-padding;
                    margin-bottom: 0;
                }
                > dd {
                    margin-bottom: 0;
                    padding-left: 0;
                }
                + div {
                    margin-top: $section-ext-dd-bottom-margin;
                }
            }
        }

        &.as-dl-table {
            > dl {
                border-top: $section-ext-dl-table-border;
                border-left: $section-ext-dl-table-border;
            }
        }
    }
}

